// ====================================
// Header's style
// ====================================

/*
* TABLE OF CONTENTS
*
* header style
*/

// > header style
.header {
    background-color: #ebebeb;
    .top{
        @include clearfix();
        font-size: 10px;
        text-transform: uppercase;
        background-color: $third-color;
        color: #fff;
        .l-grid{
            height: 36px;
            padding-top: 8px;
        }
    }
    &__stock{
        float: left;
        .item{
            height: 20px;
            display: inline-block;  
            margin-left: 20px;
            &:first-child{
                margin-left: 0;
            }
            .name{
                display: inline-block;
            }
            .number{
                margin-left: 5px;
                display: inline-block;
                height: 20px;
                padding: 0 10px 0 20px;
                border-radius: 10px;
                line-height: 18px;
                border: 1px solid #ccc;
                color: #ccc;
                position: relative;
                &:before{
                    content : '‑';
                    height: 18px;
                    width: 18px;
                    text-align: center;
                    line-height: 14px;
                    position: absolute;
                    left: 2px;
                    top: 0;
                    color: #ccc;
                }
            }
            .up{
                border: 1px solid #7ED321;
                color: #7ED321;
                &:before{
                    content : '↑';
                    color: #7ED321;
                }
            }
            .down{
                &:before{
                    content : '↓';
                    color: #FF0E2C;
                }
                border: 1px solid #FF0E2C;
                color: #FF0E2C;
            }
            .stay{}
        }
        .item:nth-child(4),
        .item:nth-child(5),{
            .number{
                padding-left: 10px;
                &:before{
                    display: none;
                }
            }
        }
    }
    &__publisher{
        float: right;
        .item{
            display: inline-block;
            margin-right: 20px;
            line-height: 20px;
            &:last-child{
                margin-right: 0;
            }
            b{
                color: $second-color;
            }
            a{
                color: #fff;
                &:hover{
                    color: rgba(white, .5);
                }
            }
        }
    }
    time{
        font-size: 12px;
        opacity: .7;
        position: absolute;
        left: 0;
        top: 52px;
        color: #555;
    }
    .l-grid {
        @include clearfix();
        height: 120px;
    }
    &__logo {
        position: absolute;
        top: 22px;
        left: calc( 50% - 120px);
        width: 239px;
        height: 78px;
        &:hover {
            opacity: .8;
        }
    }
    &__search {
        position: absolute;
        right: 0px;
        top: 45px;
        height: 30px;
        color: #fff;
        .search{
            height: 30px;
            width: 235px;
            background-color: #FFFFFF;
            border-radius: 4px;
            overflow: hidden;
            color: #999;
            button{
                width: 30px;
                height: 30px;
                border: none;
                padding: 0;
                background-color: transparent;
            }
            input{
                padding: 0;
                vertical-align: top;
                border: none;
                width: 200px;
                height: 30px;
                @include font(14,20,$normal);
                padding-left: 10px;
            }
        }
    }
}