* sapo
*/

// Note: wrap all article'elements exept sidebar

.article-item{
    .breadcrumbs{
        margin: 20px 0px;
    }
}

.article {
    padding-bottom: 20px;
    @include clearfix();
    position: relative;
    /*> header*/
    > .l-grid{
        background-color: #fff;
    }
    &__header{
        .subtitle-01{
            @include font(16,22);
            margin-bottom: 10px;
            text-decoration: underline;
            color: $first-color;
        }
        .subtitle-02{
            @include font(16,22,$normal);
            margin-bottom: 10px;
            text-transform: none;
            font-style: italic;
        }
    }
    .article__meta {
        color: #000;
        font-size: 0;
        vertical-align: middle;
        font-size: 0;
        @include clearfix();
        position: relative;
        &:before{
            content : '';
            width: 200px;
            height: 1px;
            background-color: #000;
            position: absolute;
            top: 0px;
            left: calc(50% - 100px);
        }
        > * {
            margin-right: 8px;
            padding-right: 8px;
            position: relative;
            @include font( 12, 22, $normal);
            &:after {
                content: '';
                width: 1px;
                height: 12px;
                top: 4px;
                right: 0;
                position: absolute;
                background-color: #000;
            }
            &:last-child {
                padding-right: 0;
                margin-right: 0;
                &:after {
                    display: none;
                }
            }
        }
        span,
        a {
            vertical-align: top;
            display: inline-block;
            color: #000;
            &:hover {
                color: $first-color;
            }
        }
        .author{
            display: inline-block;
            a{
                color: #DE0202;
                font-weight: bold;
                margin-left: 3px;
            }
        }
        .comment{
            i{
                vertical-align: top;
                margin-top: 2px;
            }
        }
    }
    .article__avatar{
        width: 800px;
        max-width: 800px;
        margin-left: -90px;
        margin-right: -90px;
        margin-bottom: 20px;

    }
    /*> sapo*/
    &__sapo {
        @include font(20, 26, 500);
        font-style : italic;
        margin-bottom: 10px;
        margin-top: 20px;
    }
    .inner-story {
        background-color: $first-color;
        padding: 16px;
        width: 100%;
        display: block;
        margin-bottom: 10px;
        a{
            color: #ddd;
            &:hover{
                text-decoration: underline;
            }
        }
        span{
            color: #ddd;
        }
        &:hover .story__heading{
            .text{
                color: rgba(255, 255, 255, .7);
            }
        }
        .story__thumb {
            width: 150px;
            height: 110px;
            @include runaround(left, 3, 10, 0, 0);
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(16, 23);
            margin-top: 0;
            margin-bottom: 0px;
            span{
                color: #fff;
            }

        }
        .story__meta{
            margin-bottom: 5px;
        }
        .story__summary {
            @include font(14, 18,$normal);
            margin-bottom: 0;
        }
        &.story--photo {
            &:hover {
                .story__heading {
                    a:after {
                        background-position: -50px -2100px;
                    }
                }
            }
        }

        &.story--video {
            &:hover {
                .story__heading {
                    a:after {
                        background-position: -50px -2132px;
                    }
                }
            }
        }
    }
    &__inner-relate{
        padding: 10px;
        @include breakpoint(sm) {
            padding: 10px 20px;
        }
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        margin-bottom: 10px;
        .story {
            margin-bottom: 5px;
            .story__heading {
                @include breakpoint(xs) {
                    @include font(14, 20);
                }
                @include breakpoint(sm) {
                    @include font(13, 20);
                }
                margin: 0;
                padding-top: 0px;
                padding-left: 10px;
                position: relative;
                &:before {
                    content: "•";
                    display: inline-block;
                    font-size: 14px;
                    position: absolute;
                    left: 0;
                    top: 0px;
                    color: $gray-light;
                }
            }
            @include last-y;
        }
    }
    &__content-img {
        display: block;
        margin-bottom: 10px;
        a.photo {
            position: relative;
            margin-bottom: 5px;
            display: block;
            i,
            em {
                position: absolute;
                bottom: 0;
                left: 0;
            }
            img {
                max-width: 100%;
                height: auto;
                margin-left: auto;
                margin-right: auto;
            }
        }
        figcaption.fig {
            @include font(14, 18, $normal);
            font-style: italic;
            color: #777;
        }
        &.full {
            width: 800px;
            max-width: 800px;
            margin-left: -90px;
            margin-right: -90px;
            figcaption.fig {
                text-align: center;
            }
        }
        &.left {
            width: 50%;
            float: left;
            margin-left: -90px;
            margin-right: 20px;
            figcaption.fig {
                text-align: left;
            }
        }
        &.right {
            width: 50%;
            float: right;
            margin-left: 20px;
            margin-right: -90px;
            figcaption.fig {
                text-align: right;
            }
        }
    } 
    &__quote {
        color: #fff;
        padding: 30px;
        margin: 5px 20px 10px 0px;
        width: 40%;
        background-color: $first-color;
        float: left;
        margin-left: -90px;
        .text {
            text-indent: 70px;
            padding-top: 40px;
            display: inline-block;
            position: relative;
            &:before{
                background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                background-size: ($sprite-w + px) ($sprite-h + px);
                display: inline-block;
                vertical-align: middle;
                background-position: 0 -2300px;
                content: '';
                width: 60px;
                height: 60px;
                position: absolute;
                top: 0px;
                left: 0px;
            }
        }
        .author {
            font-style: normal;
            text-align: left;
            font-weight: bold;
            display: block;
            padding-top: 25px;
            position: relative;
            &:before{
                background-color: #fff;
                content: '';
                width: 60%;
                height: 1px;
                position: absolute;
                top: 10px;
                left: 0px;
            }
        }
    }
    &__notice{
        background-color: #d9edf7;
        border-color: #bce8f1;
        color: #31708f;
        padding: 16px;
    }
    &__author{
        text-align: right;
        font-weight: bold;
    }
    &__social {
        @include clearfix;
        font-size: 0;
        margin-bottom: 10px;
        z-index: 90;
        ul {
            float: left;
            margin-right: 5px;
            li {
                float: left;
                margin-right: 5px;
                @include last-x;
            }
        }
        .tool {
            float: right;
            display: block;
            a {
                height: 20px;
                line-height: 20px;
                border-radius: 3px;
                display: inline-block;
                padding: 0 5px;
                font-size: 11px;
                color: #fff;
                i {
                    margin-right: 5px;
                }
            }
            a.print {
                background-color: #999;
                @include breakpoint(xs) {
                    display: none;
                }
            }
        }
    }
    &__tag{
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        margin-bottom: 20px;
        padding: 10px 0;
        @include clearfix;
        .title {
            @include font(15, 30);
            padding: 0 15px 0 0;
            display: inline-block;
            text-transform: uppercase;
            float: left;
            margin-top: 5px;
        }
        p {
            @include font(15, 20, $normal);
            margin-left: 65px;
            padding-top: 4px;
            a {
                border-radius: 15px;
                display: inline-block;
                margin-bottom: 5px;
                padding: 5px 10px;
                border-radius: 3px
            }
        }
    }
    &__body {
        // Note: padding, margin, fontstyle among article body
        overflow-wrap: break-word;
        @include clearfix();
        @include font( 15, 22, $normal);
        color : #000;
        p,
        div,
        h3,
        h4,
        h5 {
            margin-bottom: 15px;
        }
        ins{
            text-decoration: none;
        }
        img {
            max-width: 100%;
            display: block;
            margin: auto;
            transform: translate3d(0,0,0);
        }
        table {
            margin: auto;
            max-width: 620px;
        }
        .body-image {
            text-align: center;
            .photo {
                display: inline-block;
                position: relative;
                &:after {
                    background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                    background-color: rgba(0, 0, 0, .5);
                    background-position: -0px -2900px;
                    vertical-align: middle;
                    background-size: ($sprite-w + px) ($sprite-h + px);
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    content: '';
                    width: 44px;
                    height: 44px;
                    border-radius: 50%;
                }
            }
            .photo:hover {
                &:after {
                    background-color: rgba(47, 161, 179, .5);
                }
            }
        }
        > * {
            max-width: 100%;
        }
        .report-table{
            width: 800px;
            max-width: 800px;
            margin-left: -90px;
            border-collapse: collapse;
            p{
                margin-bottom: 0;
            }
            strong{
                font-size: 16px;
            }
            tbody{
                td{
                    padding: 10px 20px;
                }
                tr:nth-of-type(odd){
                    background-color: #f9f9f9;
                }
                tr:first-child{
                    background-color: $first-color;
                    font-weight: bold;
                    color: #fff;
                }
            }
            td:first-child{
                width: 120px;
            }
        }
    }
    &__comment{
        margin-bottom: 20px;
        margin-top: 20px;
        @include clearfix;
        > .title {
            @include font(28, 34);
            margin-bottom: 10px;
            display: block;
        }
        .user {
            margin-bottom: 10px;
        }
        .comment {
            border-bottom: 1px solid #eee;
            margin-bottom: 10px;
            padding-bottom: 10px;
            @include last-y;
        }
        .avatar {
            height: 25px;
            width: 25px;
            border-radius: 50%;
            border: 1px solid #ccc;
            display: inline-block;
            float: left;
            position: relative;
            i {
                font-size: 12px;
                color: #ccc;
                position: absolute;
                top: 5px;
                left: 7px;
            }
        }
        .name {
            @include font(13, 18);
            text-transform: uppercase;
            margin-bottom: 5px;
        }
        .text {
            @include font(15, 20,$normal);
            color: #555;
            margin-bottom: 5px;
        }
        .tool {
            a {
                font-size: 12px;
                color: #777;
                display: inline-block;
                margin-right: 10px;
                &:hover {
                    color: $first-color;
                }
                i {
                    font-size: 14px;
                    margin-right: 5px;
                }
            }
        }
        .reply {
            padding: 10px 0 0 25px;
            text-align: right;
            textarea {
                border-radius: 3px;
                border: 1px solid #ddd;
                width: 100%;
                height: 50px;
                padding: 5px;
                font-size: 15px;
                line-height: 20px;
            }
            .btnSubmit {
                display: inline-block;
                margin-bottom: 0;
                font-weight: 400;
                text-align: center;
                touch-action: manipulation;
                cursor: pointer;
                border: 1px solid transparent;
                white-space: nowrap;
                padding: 3px 5px;
                font-size: 15px;
                line-height: 1.42857;
                border-radius: 0;
                -ms-user-select: none;
                user-select: none;
                border-radius: 3px;
                background-color: $first-color;
                color: #fff;
                font-size: 12px;
                line-height: 1.5;
                &:hover {
                    background-color: darken($first-color, 10%);
                }
                &.disabled {
                    cursor: default;
                }
            }
        }
        .sub-comments {
            margin-top: 10px;
            padding: 32px;
            margin-left: 25px;
            background-color: rgba($first-color, .1);
        }
        .input {
            @include clearfix;
            textarea {
                border-radius: 3px;
                border: 1px solid #ddd;
                width: 100%;
                height: 70px;
                padding: 10px;
            }
            p.note {
                font-style: italic;
                font-size: 11px;
                float: left;
                display: inline-block;
                color: #999;
                @include breakpoint(xs) {
                    line-height: 17px;
                    width: 60%;
                }
                @include breakpoint(sm) {
                    line-height: 31px;
                }
            }
            button {
                border: none;
                border-radius: 3px;
                float: right;
                background-color: $first-color;
                color: #fff;
                @include font(14,32,$normal);
                padding-left: 10px;
                padding-right: 10px;
                &:hover {
                    background-color: darken($first-color, 10%);
                }
            }
        }
    }
    &__hot-video{
        margin-bottom: 30px;
        .title {
            @include font(20, 55);
            background-color: #f5f5f5;
            border-top: 1px solid #77c2af;
            text-transform: uppercase;
            padding-left: 15px;
        }
        .content {
            overflow: hidden;
            position: relative;
            @include breakpoint(sm) {
                background-color: #f5f5f5;
                text-align: center;
            }
        }
    }
    &__outer-relate{
        margin-bottom: 30px;
        padding-left: 90px;
        @include clearfix();
        &:last-child{
            margin-bottom: 0;
        }
        > .title{
            @include font(28,34);
            margin-bottom: 15px;
            display: block;
            text-align: center;
        }
        .story{
            width: 245px;
            margin-right: 16px;
            float: left;
            padding-bottom: 15px;
            &:nth-child(4n){
                margin-right: 0;
            }
        }
        .story__thumb{
            margin-bottom: 5px;
        }
        .story__heading{
            .cate{
                margin-top: 4px;
            }
        }
    }
    &__footer-relate{
        background-color: #F6F6F6;
        padding: 15px;
        .story{
            margin-top: 20px;
            &:first-child{
                margin-top: 0;
            }
        }
        .story__heading{
            @include font(18,24);
            margin-bottom: 5px;
        }
        .story__thumb{
            width: 180px;
            height: 110px;
            @include runaround(left, 3, 15, 0, 0);
        }
    }
}

.two-cols{
    .col-800{
        float: left;
        position: relative;
        padding: 0 90px;
    }
    .col-300{
        float: right;
        > * {
            margin-bottom: 16px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    .article{
        position: relative;
        .article__author-pin{
            position: absolute;
            left: -146px;
            top: 0;
            width: 130px;
            background-color: $first-color;
            color: #fff;
            padding: 10px;
    
            .author{
                padding-bottom: 10px;
            }
            .thumb {
                width: 47px;
                height: 47px;
                border-radius: 50%;
                overflow: hidden;
                background-color: #000;
                margin: 0px auto 10px;
                box-shadow: 0 0 10px rgba(black, .3);
                img {
                    width: 47px;
                    height: 47px;
                }
            }
            .desc{
                display: block;
                @include font(12,16,$normal);
                text-align: center;
            }
            .name{
                display: block;
                @include font(16,20);
                text-align: center;
            }
            .stories{
                padding-top: 20px;
                    }
            .stories__title{
                @include font(12,16,$normal);
                display: block;
                position: relative;
                padding-bottom: 20px;
                &:before{
                    position: absolute;
                    left: 0;
                    top: -15px;
                    width: 35px;
                    height: 1px;
                    background-color: rgba(white, .3);
                    content: '';
                }
            }
            .story{
                padding-bottom: 20px;
                a{
                    color: #fff;
                    &:hover{
                        text-decoration: underline;
                    }
                }
                span{
                    color: #fff !important;
                }
                &:hover .story__heading{
                    .text{
                        color: #fff;
                        text-decoration: underline;
                    }
                }
            }
            .story__heading{
                @include font(14,18,600);
            }
        }
    }
}

.solid-bg{
    .article{
        .article__header {
            width: 800px;
            margin-left: -90px;
            margin-right: -90px;
            background-color: #fff;
            @include clearfix();
            text-align: center;
            >.l-grid{
                padding-right: 300px;
            }
            h1{
                @include font( 30, 40);
                font-family: $font-family-sans-serif;
                margin-bottom: 10px;
            }
        }
        .article__meta{
            .spr--comment{
                background-position: 0 -2150px;
            }
            text-align: center;
            padding-top: 15px;
        }
    }
}

/* > Q-A article */
.article__qa{
    border-top: 5px solid $first-color;
    padding-top: 20px;
    margin-left: -90px;
    margin-right: -90px;
    width: 800px;
    max-width: 800px;
    .item{
        padding-top: 20px;
        margin-top: 20px;
        border-top: 1px solid #ddd;
        @include first-y;
    }
    .question{
        @include clearfix();
        .user{
            text-align: center;
            @include font(12,16);
            width: 100px;
            float: left;
            &:after{
                display: block;
                content: '';
                width: 50px;
                height: 50px;
                margin: 10px auto 0;
                background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                background-size: ($sprite-w + px) ($sprite-h + px);
                background-position: 0 -2250px;
            }
        }
        .content{
            width: 668px;
            float: right;
            time{
                display: block;
                @include font(12,16,$normal);
                color: #999;
            }
            .text{
                margin-top: 10px;
                @include font(14,18,$normal);
                font-style : italic;
                display: inline-block;
            }
        }
    }
    .answer{
        @include clearfix();
        .user{
            text-align: center;
            width: 150px;
            padding: 10px;
            padding-top: 0;
            float: left;
            .thumb{
                width: 65px;
                height: 65px;
                border-radius: 50%;
                overflow: hidden;
                display: block;
                margin: 0px auto 0;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .name{
                @include font(16,20);
                display: block;
                margin-top: 10px;
            }
            .desc{
                margin-top: 10px;
                display: block;
                @include font(14,18,$normal);
            }
        }
        .content{
            padding: 20px;
            background-color: #EFF6FF;
            width: 618px;
            float: right;
            @include font(14,18,$normal);
        }
    }
}


