// ====================================
// Customize breadcrumbs
// ====================================

/*
* TABLE OF CONTENTS
*
* LOGO + LIVE ICON +
* --- header
* --- social
* --- story
* --- box icon
*/

// > LOGO + LIVE ICON + 
.logo-color {
    background-image: url('../img/desktop-logo-1.0.0.png');
    display: inline-block;
    vertical-align: middle;
    background-size: 300px 150px;
    background-position: -0px -0px;
    width: 239px;
    height: 78px;
    transform: scale(1.3);
}
.logo-solid {
    background-image: url('../img/desktop-logo-1.0.0.png');
    display: inline-block;
    vertical-align: middle;
    background-size: 300px 150px;
    background-position: -0px -78px;
    width: 141px;
    height: 35px;
}


.spr {
    background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
    background-size: ($sprite-w + px) ($sprite-h + px);
    display: inline-block;
    vertical-align: middle;
    /* > --- header */
    &--search {
        background-position: -0px -0px;
        width: 20px;
        height: 20px;
    }
    &--home {
        background-position: -0px -50px;
        width: 24px;
        height: 24px;
    }
    &--coin{
        background-position: -0px -100px;
        width: 28px;
        height: 28px;
    }
    &--newspaper{
        background-position: -0px -150px;
        width: 28px;
        height: 28px;
    }
    &--clamp{
        background-position: -0px -200px;
        width: 28px;
        height: 28px;
    }
    &--menubar{
        background-position: -0px -250px;
        width: 24px;
        height: 24px;
    }
    /* > --- pin-menu */
    &--pin-zalo{
        background-position: -0px -1200px;
        width: 20px;
        height: 20px;
    }
    &--pin-fb{
        background-position: -0px -1250px;
        width: 20px;
        height: 20px;
    }
    &--pin-gplus{
        background-position: -0px -1300px;
        width: 20px;
        height: 20px;
    }
    &--pin-comment{
        background-position: -0px -1350px;
        width: 20px;
        height: 20px;
    }
    /* > --- social */
    &--fb {
        background-position: -0px -3400px;
        width: 32px;
        height: 32px;
    }
    &--zalo {
        background-position: -0px -3350px;
        width: 32px;
        height: 32px;
    }
    &--comment{
        background-position: -0px -2400px;
        width: 16px;
        height: 16px;
    }
    &--thumb-up{
        background-position: -50px -2484px;
        width: 16px;
        height: 16px;
    }
    &--thumb-down{
        background-position: -84px -2484px;
        width: 16px;
        height: 16px;
    }
}
