// > footer style
.footer {
    background-color: $third-color;
    position: relative;
    color: #fff;
    &__logo {
        position: absolute;
        top: 30px;
        left: calc(50% - 70px);
        width: 141px;
        height: 35px;
        &:hover {
            opacity: .8;
        }
    }
    &__copyRight{
        @include clearfix();
        padding: 30px 0;
        @include font(12,18,$normal);
        color: rgba(255, 255, 255, .5);
        .left{
            float: left;
            width: 60%;
        }
        .right{
            width: 40%;
            float: right;
            text-align: right;
        }
        a{
            font-weight: bold;
            color: rgba(255, 255, 255, .5);
        }
    }
    &__share{

    }
}
