/* > GRID  */

// list speccify page
// .nhip-song-thu-do  // chuyên trang, khac nhau chỗ above the fold
// .cong-nghe  // chuyên trang, khac nhau chỗ above the fold
// .category-page  // Trang danh mục
.l-grid {
    position: relative;
    width: 1120px;
    margin: 0 auto;
    @include clearfix;
}

.col-160 {
    width: 160px;
}

.col-220 {
    width: 220px;
}

.col-300 {
    width: 300px;
}

.col-480 {
    width: 480px;
}

.col-560 {
    width: 560px;
}

.col-625 {
    width: 625px;
}

.col-800 {
    width: 800px;
}

.wrapper {
    @include clearfix;
    position: relative;
}

.header {}

.abf {
    @include clearfix();
}
.home-page{
    .main-container {
        padding: 15px 0 50px;
    }
    .l-grid{
        @include clearfix();
    }
    .l-grid >[class^="col-"] {
        margin-right: 20px;
        float: left;
        &:last-child {
            margin-right: 0;
        }
    }
    .col-480 {
        > div{
            margin-top: 30px;
            &:first-child{
                margin-top: 0;
            }
        }
    }
    .col-300 {
        > div{
            margin-top: 30px;
            &:first-child{
                margin-top: 0;
            }
        }
    }
}

.category-page{
    .main-container{
        padding: 20px 0;
    }
    .l-grid{
        @include clearfix();
        padding-bottom: 20px;
    }
    .l-grid >[class^="col-"] {
        margin-right: 20px;
        float: left;
        &:last-child {
            margin-right: 0;
        }
    }
    .col-800 {
        .cate-highlight{
            margin-top: 20px;
        }
        .cate-highlight + .clearfix{
            padding-top: 30px;
        }
        .col-625{
            float: left;
        }
        .col-160{
            float: right;
            .fyi--160{
                margin-top: 20px;
            }
        }
    }
    .col-300 {
        > div{
            margin-top: 30px;
            &:first-child{
                margin-top: 0;
            }
        }
    }
    
}
.footer {}


