// > main navigation 
.nav-wrap{
    @include clearfix();
    background-color: #E5E5E5;
}
.nav {
    background-color: #E5E5E5;
    border-top: 1px solid #c2c2c2;
    border-bottom: 1px solid #c2c2c2;
    height: 40px;
    &__parent {
        float: left;
        height: 38px;
        position: relative;
        > a {
            @include font( 12, 38);
            height: 38px;
            text-transform: uppercase;
            display: block;
            padding: 0px 20px;
            border-left: 1px solid #c2c2c2;
            cursor: pointer;
        }
        &:hover .nav__child {
            left: -15px;
            display: block;
            z-index: 2147483647;
        }
        &:hover,
        &.is-active{
            background-color: #f7f7f7;
            > a{
                color: $first-color;
            }
        }
        &:first-child{
            > a{
                padding: 0 10px;
                i{
                    vertical-align: top;
                    margin-top: 5px;
                }
            }
        }
        &.last-child{
            float: right;
            > a{
                background-color: #f7f7f7;
                padding: 0 9px;
                i{
                    vertical-align: top;
                    margin-top: 5px;
                }
            }
            .link{
                a{
                    font-weight: bold !important;
                }
            }
        }
        &:last-child:hover .nav__child ,
        &:nth-last-child(2):hover .nav__child ,
        {
            right: 0px;
            left: inherit;
            display: block;
            z-index: 2147483647;
        }
    }
    &__child {
        position: absolute;
        left: -999em;
        width: 250px;
        background: #FFFFFF;
        box-shadow: 0 2px 10px 0 rgba(0,0,0,0.20);
        z-index: 10;
        padding: 15px;
        .link{
            a{
                display: block;
                height: 45px;
                @include font(12,45,$normal);
                padding: 0px 8px;
                text-transform: uppercase;
                border-top: 1px solid #eee;
                &.is-active,
                &:hover{
                    text-decoration: underline;
                }
                &:first-child{
                    border-top: none;
                }
            }
        }
    }
}

.second-nav {
    background-color: $first-color;
    height: 40px;
    &__parent {
        float: left;
        height: 40px;
        position: relative;
        margin-right: 20px;
        > a {
            @include font( 12, 40);
            height: 40px;
            text-transform: uppercase;
            display: block;
            padding: 0px 30px 0 20px;
            cursor: pointer;
            color: #fff;
            // background-image: url('../img/arrow-'+$arrow-version+'.png');
            // background-size: ($arrow-w + px) ($arrow-h + px);
            // background-repeat: no-repeat;
            // background-position: right -3px top 16px;
            span{
                background-color: $third-color;
                display: inline-block;
                height: 30px;
                line-height: 30px;
                vertical-align: bottom;
                margin-right: 10px;
                padding: 0 3px;
                border-top-right-radius: 3px;
                border-top-left-radius: 3px;
            }
        }
        &:first-child{
            > a{
                padding-left: 0;
            }
        }
        &:hover .second-nav__child {
            right: 0px;
            display: block;
            z-index: 2147483647;
        }
        &:hover,
        &.is-active{
            > a{
            }
        }
    }
    &__child {
        position: absolute;
        right: -999em;
        width: 250px;
        background: #FFFFFF;
        box-shadow: 0 2px 10px 0 rgba(0,0,0,0.20);
        z-index: 10;
        padding: 15px;
        display: none;
        .link{
            a{
                display: block;
                height: 45px;
                @include font(12,45,$normal);
                padding: 0px 8px;
                text-transform: uppercase;
                border-top: 1px solid #eee;
                &.is-active,
                &:hover{
                    text-decoration: underline;
                }
                &:first-child{
                    border-top: none;
                }
            }
        }
    }
}

