// ====================================
// Story's style
// ====================================

/*
* TABLE OF CONTENTS
*
* no image in article
* normal story
* story type
*/

// > no image in article
.no-fig {
    .story__thumb a {
        width: 100%;
        height: 100%;
        display: block;
        background: #f7f7f7;
        text-align: center;
        text-transform: uppercase;
        color: #000;
        font-size: 10px;
        font-size: 1.0rem;

        &:after {
            content: 'Không có ảnh' !important;
            display: block;
            padding-top: 28%;
            width: 100% !important;
            height: 100% !important;
            background-image: none !important;
            background-color: transparent !important;
            top: 0 !important;
            left: 0 !important;
            right: 0 !important;
            bottom: 0 !important;
        }

        img {
            display: none;
        }
    }
}

// > normal story
.story {
    @include clearfix;

    &__thumb {
        overflow: hidden;
        background-color: #eee;

        img {
            width: 100%;
            height: auto;
            transition: transform .5s ease-out .1s;
            text-indent: -9999px;
            transform: translate3d(0, 0, 0);
        }

        &:hover {
            overflow: hidden;

            img {
                // opacity: .8;
                -webkit-transform: scale($rate-zoom);
                /* Safari and Chrome */
                -moz-transform: scale($rate-zoom);
                /* Firefox */
                -ms-transform: scale($rate-zoom);
                /* IE 9 */
                -o-transform: scale($rate-zoom);
                /* Opera */
                transform: scale($rate-zoom);
            }
        }
    }

    &__heading {
        display: block;
        font-size: 16px;
        line-height: 20px;
        font-weight: 500;

        a:visited {
            // color: #999;
        }
    }

    &__meta {
        color: $grey-color;
        font-size: 0;
        height: 20px;
        line-height: 20px;
        vertical-align: middle;
        overflow: hidden;
        @include clearfix();
        font-size: 0;

        >* {
            position: relative;
            @include font(12, 20, $normal);
            margin-left: 20px;
            color: #000;

            &:first-child {
                margin-left: 0;
            }
        }

        .author {
            a {
                font-weight: bold;
                color: $first-color;
            }
        }
    }

    &__summary {
        @include font(14, 20, $normal);
    }

    &__refer {
        a {
            display: block;
            color: #616161;
            padding-left: 14px;
            position: relative;
            @include font(12, 16, $normal);

            &:before {
                content: "";
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: #B8B8B8;
                position: absolute;
                left: 0px;
                top: 5px;
            }
        }
    }

    &:hover {
        .story__heading {
            a {
                color: $first-color;
            }
        }
    }

    &:focus {
        background-color: red;
    }
}

/* story type */

.story--video,
.story--photo,
.story--live,
    {
    .story__heading {
        >a:after {
            background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
            background-size: ($sprite-w + px) ($sprite-h + px);
            content: '';
            width: 16px;
            height: 16px;
            display: inline-block;
            vertical-align: middle;
            margin: 0 5px;
        }
    }
}

.story--photo {
    .story__heading {
        a:after {
            background-position: -50px -2400px;
        }
    }

    &:hover {
        .story__heading {
            a:after {
                background-position: -84px -2400px;
            }
        }
    }
}

.story--video {
    .story__heading {
        a:after {
            background-position: -50px -2434px;
        }
    }

    &:hover {
        .story__heading {
            a:after {
                background-position: -84px -2434px;
            }
        }
    }
}

.story--live {
    .story__heading {
        a:after {
            width: 32px;
            background-position: -50px -2450px;
            // background-color: rgba(255, 0, 0, .7);
            // -webkit-animation: animation-blink 1s infinite;
            // -moz-animation: animation-blink 1s infinite;
            // -o-animation: animation-blink 1s infinite;
            // animation: animation-blink 1s infinite;
        }
    }

    &:hover {
        .story__heading {
            a:after {
                background-position: -50px -2450px;
            }
        }
    }
}

.story--infographic,
.story--magazine,
    {
    .story__heading {
        >a:after {
            background-color: #333;
            content: '';
            height: 16px;
            line-height: 16px;
            display: inline-block;
            vertical-align: middle;
            padding: 0 5px;
            margin: 0 5px;
            color: #fff;
            font-size: 10px;
            font-weight: normal;
        }
    }

    &:hover {
        .story__heading {
            a:after {
                background-color: $first-color;
            }
        }
    }
}

.story--infographic {
    .story__heading {
        a:after {
            content: 'infographic';
        }
    }
}

.story--magazine {
    .story__heading {
        a:after {
            content: 'magazine';
        }
    }
}