// > box heading
.box-heading.general {
    .main {
        height: 22px;
        overflow: hidden;
        line-height: 22px;
        border-left: 5px solid $first-color;
        padding-left: 10px;

        span {
            text-transform: uppercase;
            display: block;
            @include font(18, 22);
            color: $first-color;
        }
    }
}

// > list with number
.list-number {
    counter-reset: myIndex;

    .box-heading {
        padding-bottom: 10px;
    }

    &__content {}

    .story {
        position: relative;
        border-bottom: 1px solid #e7e7e7;
        padding-top: 5px;
        padding-bottom: 15px;

        &:last-child {
            border-bottom: none;
        }
    }

    .story:before {
        counter-increment: myIndex;
        content: counter(myIndex) "  ";
        @include font(48, 48, $normal);
        width: 30px;
        text-align: center;
        position: absolute;
        top: 5px;
        left: 0;
        color: #E5E5E5;
    }

    .story__heading {
        padding: 15px 5px 0 55px;

        .text {
            @include font(14, 20);
        }
    }

    .story__meta {
        padding-left: 55px;

        .author,
        time {
            display: none;
        }
    }

    &.image-bg {
        .story {
            overflow: hidden;
            color: #fff;

            a,
            span {
                color: #fff;
            }
        }

        .story:before {
            z-index: 10;
        }

        .story__thumb {
            position: absolute;
            top: -5px;

            &:after {
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                background-color: rgba(black, .7);
            }
        }

        .story__heading {
            position: relative;
            z-index: 10;
        }
    }
}

// > magazine
.magazine {
    background-color: $first-color;
    padding: 10px 40px 20px;
    max-height: 315px;
    overflow: hidden;

    .box-heading {
        h2 {
            text-align: center;
            position: relative;

            &:before,
            &:after {
                content: '';
                position: absolute;
                display: block;
                width: 70px;
                height: 1px;
                background-color: #fff;
                top: 18px;
            }

            &:before {
                left: 0;
            }

            &:after {
                right: 0;
            }
        }

        a {
            @include font(18, 22);
            color: #fff;
            text-transform: uppercase;
            position: relative;

            &:hover {
                span {
                    color: #fff;
                    opacity: .7;
                }
            }

            &:before,
            &:after {
                content: '';
                position: absolute;
                display: block;
                width: 90px;
                height: 1px;
                background-color: #fff;
                top: 15px;
            }

            &:before {
                left: -101px;
            }

            &:after {
                right: -101px;
            }
        }
    }

    .box-content {
        padding-top: 20px;

        .item {
            width: 105px;
            display: block;

            img {
                border: 1px solid #fff;
                display: block;
                margin-bottom: 5px;
                max-width: 105px;
            }

            span {
                @include font(12, 16, $normal);
                color: #fff;
                display: block;
            }
        }
    }

    .bx-controls {
        font-size: 0;
        height: 22px;
        padding-top: 10px;
        text-align: center;
    }

    .bx-pager-item {
        display: inline-block;

        a {
            display: block;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, .3);
            text-indent: -999em;
            margin: 0 5px;
        }

        .active {
            background-color: #fff;
        }
    }
}

// > box-one-story
.box-one-story {
    .box-heading {
        h2 {
            @include font(18, 22);
             text-transform: uppercase;
        }

        a {
            color: $first-color;
        }
    }

    .box-content {
        margin-top: 5px;
        border: 4px solid $second-color;
        background-color: $first-color;
        padding: 10px 10px 15px 15px;
        position: relative;

        .story {
            height: 228px;
            overflow: hidden;

            span,
            a {
                color: #fff;
            }
        }

        .story__heading {
            @include font(18, 24, 500);
            padding-bottom: 5px;
        }

        .story__thumb {
            width: 110px;
            height: 60px;
            border: 1px solid #fff;
            @include runaround(left, 5, 10, 5, 0);
        }

        .story__meta {
            display: none;
        }

        .story__summary {
            @include font(14, 19, $normal);
        }
    }

    .bx-controls {
        font-size: 0;
        height: 22px;
        padding-top: 10px;
        text-align: center;
    }

    .bx-pager-item {
        display: inline-block;

        a {
            display: block;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, .3);
            text-indent: -999em;
            margin: 0 5px;
        }

        .active {
            background-color: #fff;
        }
    }

    .show-more {
        display: block;
        position: absolute;
        bottom: 0px;
        right: 0px;
        background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
        background-size: ($sprite-w + px) ($sprite-h + px);
        background-position: 0 -1450px;
        width: 30px;
        height: 30px;

        &:hover {
            opacity: .8;
        }
    }
}

// > box-two-border
.box-two-border {
    .box-heading {
        h2 {
            @include font(18, 22);
            text-transform: uppercase;
        }

        a:hover {
            color: $first-color;
        }

        .main {
            height: 22px;
            overflow: hidden;
            line-height: 22px;
            border-left: 5px solid $first-color;
            padding-left: 10px;

            span {
                text-transform: uppercase;
                display: block;
                @include font(18, 22);
                color: $first-color;
            }
        }
    }

    .box-content {
        .story {
            border-top: 1px solid #e7e7e7;
            padding-top: 12px;
            margin-top: 12px;
        }

        .first-story {
            padding-top: 0px;
            margin-top: 0;
            border-top: none;

            .story__heading {
                @include font(20, 26, 500);
                margin-bottom: 5px;
            }

            .story__meta {
                display: block;
            }
        }

        .story__thumb {
            width: 100%;
            height: 170px;
            margin-bottom: 10px;
        }

        .story__heading {
            @include font(16, 22, 500);
            margin-bottom: 5px;
        }

        .story__meta {
            display: none;
        }

        .story__summary {
            margin-top: 10px;
        }
    }
}



// > portrait-box
.portrait-box {
    .box-heading {
        background: $first-color;
        margin-bottom: 0;

        h2 {
            @include font(18, 45);
            text-transform: uppercase;
            padding-left: 10px;
        }

        a {
            color: #fff;

            &:hover {
                span {
                    color: rgba(white, .5);
                }
            }
        }
    }

    .box-content {
        position: relative;

        a {

            // color: #fff;
            &:hover {
                text-decoration: underline;
            }
        }
    }

    .story {
        height: 228px;
        overflow: hidden;
    }

    .story__heading {
        @include font(18, 24);
        padding-bottom: 5px;
    }

    .story__thumb {
        width: 110px;
        height: 60px;
        border: 1px solid #fff;
        @include runaround(left, 5, 10, 5, 0);
    }

    .portrait {
        @include clearfix();
        position: relative;

        span,
        a {
            color: #fff;
        }
    }

    .thumb {
        width: 100%;
        height: 200px;
        overflow: hidden;
        background-color: #000;
        margin-right: 15px;
        float: left;

        img {
            width: 100%;
            height: 100%;
        }

        &:hover {
            opacity: .8;
        }
    }

    .text {
        position: absolute;
        padding: 20px;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, .6);
        padding: 10px;

    }

    .name {
        @include font(16, 20);
        display: block;
        text-transform: uppercase;
    }

    .desc {
        font-size: 14px;
    }

    .quote {
        border-left: 1px solid #ddd;
        border-right: 1px solid #ddd;
        @include font(18, 26);
        padding: 25px;
        position: relative;
        text-indent: 50px;

        &:before {
            position: absolute;
            left: 25px;
            top: 15px;
            content: '';
            width: 40px;
            height: 40px;
            background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
            background-size: ($sprite-w + px) ($sprite-h + px);
            display: inline-block;
            vertical-align: middle;
            background-position: -50px -2250px;
        }
    }

    .bx-default-pager {
        top: -25px;
        right: 10px;
        position: absolute;
    }

    .bx-pager-item {
        display: inline-block;

        a {
            display: block;
            width: 10px;
            height: 2px;
            background-color: rgba(255, 255, 255, .3);
            text-indent: -999em;
            margin: 0 2px;
        }

        .active {
            background-color: #fff;
        }
    }

}

// > banner-box
.banner-box {
    .item {
        display: block;
        margin-top: 10px;

        &:first-child {
            margin-top: 0;
        }
    }
}

// > fullSize-box-01
.fullSize-box-01 {
    background-image: linear-gradient(89deg, #067BC2 0%, #759F66 100%);

    .box-heading {
        @include clearfix();
        padding-top: 25px;

        a {
            color: #fff;
            text-transform: uppercase;
        }

        .main {
            @include font(24, 30);
            float: left;
        }

        .sub {
            text-align: right;

            h3 {
                display: inline-block;
                margin: 0 10px;

                a {
                    display: block;
                    float: right;
                    @include font(14, 32, $normal);
                    height: 30px;
                    background-color: rgba(0, 0, 0, .1);
                    padding: 0 15px;
                    border-radius: 16px;
                }

                a:hover {
                    background-color: rgba(0, 0, 0, .3);
                }
            }
        }
    }

    .box-content {
        padding-top: 12px;
        @include clearfix();
        padding-bottom: 30px;

        .col-480,
        .col-300 {
            float: left;
            margin-right: 15px;

            &:last-child {
                margin-right: 0;
            }
        }

        .story {
            color: #fff;

            time,
            a {
                color: #fff;

                &:hover {
                    span {
                        color: rgba(255, 255, 255, .7);
                    }
                }
            }
        }

        .first-story {
            .story {}

            .story__thumb {
                width: 480px;
                height: 270px;
                margin-top: 3px;
            }

            .story__heading {
                margin-top: 10px;
                @include font(24, 30);
            }

            .story__meta {
                margin-top: 10px;
            }

            .story__summary {
                margin-top: 10px;
            }
        }

        .more-stories {
            .story {
                margin-top: 12px;

                &:first-child {
                    margin-top: 0;
                }
            }

            .story__thumb {
                width: 135px;
                height: 75px;
                @include runaround(left, 3, 15, 0, 0);
            }

            .story__heading {
                @include font(14, 20);
            }
        }

        .big-stories {
            .story {
                margin-top: 25px;

                &:first-child {
                    margin-top: 0;
                }
            }

            .story__thumb {
                width: 300px;
                height: 170px;
                margin-top: 3px;
            }

            .story__heading {
                margin-top: 5px;
                @include font(14, 20);
            }
        }
    }
}

