// > abf
.abf {
    @include clearfix();
    background-color: #ebebeb;
    padding-bottom: 20px;

    .l-grid {
        padding-top: 15px;
    }

    .col-560,
    .col-300,
    .col-220 {
        float: left;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }
    }

    .col-560 {
        padding: 5px 17px 10px;
        position: relative;

        .item {
            height: 595px;
            display: block;
        }

        &:before {
            content: '';
            width: 560px;
            height: 565px;
            position: absolute;
            top: 50px;
            left: 0;
            background-color: #fff;
        }

        .bx-controls {
            font-size: 0;
            height: 22px;
            padding-top: 10px;
            text-align: center;
            position: absolute;
            left: calc(50% - 30px);
            top: 305px;
        }

        .bx-pager-item {
            display: inline-block;

            a {
                display: block;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                background-color: #ccc;
                border: 1px solid #E0E0E0;
                text-indent: -999em;
                margin: 0 5px;
            }

            .active {
                background-color: $first-color;
                border: 1px solid #ccc;
            }
        }
    }

    .first-story {
        // margin-top: -50px;
        height: 433px;
        overflow: hidden;

        .story__thumb {
            margin-bottom: 10px;
            width: 525px;
            height: 295px;
            margin-bottom: 35px;
            position: relative;
            overflow: inherit;

            a {
                display: block;
                width: 525px;
                height: 295px;
                overflow: hidden;
            }

            &:before {
                position: absolute;
                top: 40px;
                left: calc(50% - 200px);
                content: '';
                width: 400px;
                height: 251px;
                box-shadow: 0px 0px 100px rgba(0, 0, 0, .4);
            }
        }

        .story__heading {
            margin-bottom: 5px;

            .text {
                @include font(24, 30);
            }
        }

        .story__meta {
            margin-bottom: 5px;
            display: none;
        }

        .story__summary {}

        .story__refer {
            a {
                @include font(14, 18);
            }
        }
    }

    .rank-2-stories {
        @include clearfix();
        padding: 15px 0px 0px 0px;
        border-top: 1px solid #eee;
        margin-top: 15px;
        position: relative;

        .story {
            width: 255px;
            float: left;
            margin-bottom: 14px;
            vertical-align: top;

            &:nth-child(2n) {
                float: right;
            }

            &:nth-child(2n+1) {
                clear: left;
            }
        }

        .story__thumb {
            width: 70px;
            height: 40px;
            @include runaround(left, 2, 10, 0, 0);
        }

        .story__heading {
            @include font(12, 18);

            .text {}
        }
    }

    .col-220 {
        position: relative;
        margin-top: 50px;

        .wrap-bg {
            background-color: #fff;
            padding-top: 5px;
        }

        .banner-box {
            margin-top: 20px;
        }
    }

    .list-stories {
        margin-top: -50px;
        padding-bottom: 0px;

        .story {
            border-top: 1px solid #F5F5F5;
            margin-top: 10px;
            padding-top: 10px;
            @include last-y;
        }

        .story__heading {
            padding: 0 10px;
            @include font(14, 20);
        }

        .story-has-thumb {
            padding-top: 0;
            margin-top: 0;
            margin-left: 7px;
            border-top: none;

            .story__thumb {
                width: 205px;
                height: 115px;
                margin-bottom: 20px;
                position: relative;
                overflow: inherit;

                a {
                    display: block;
                    width: 205px;
                    height: 115px;
                    overflow: hidden;
                }

                &:before {
                    position: absolute;
                    top: 14px;
                    left: calc(50% - 50px);
                    content: '';
                    width: 100px;
                    height: 100px;
                    box-shadow: 0px 0px 100px rgba(0, 0, 0, .4);
                }
            }

            .story__heading {
                padding-left: 3px;
                padding-right: 3px;
                margin-top: 5px;
            }
        }
    }

    .col-300 {
        .box-one-story {
            height: 310px;
            overflow: hidden;
            // margin-top: -26px !important;
            margin-bottom: 30px;
        }
    }
}

