// > timeline-stories
.timeline-stories {
    .story {
        @include first-y;
        padding-top: 20px;
        padding-bottom: 20px;
        border-top: 1px solid #E7E7E7;
    }

    .story--focus {
        background-color: #FFFEEB;
    }

    .story__heading {
        padding-bottom: 5px;

        .text {
            @include font(18, 22);
        }
    }

    .story__meta {
        margin-bottom: 0px;
    }

    .story__thumb {
        width: 180px;
        height: 100px;
        @include runaround(left, 4, 16, 5, 0)
    }

    .story__refer {
        @include clearfix();
        font-size: 0;
        padding-top: 5px;

        a {
            display: inline-block;
            margin-top: 3px;

            &:first-child {
                margin-top: 0;
            }
        }
    }
}

.cate-timeline {
    .box-heading {}

    .box-content {
        padding-top: 15px;
    }

    .story {
        @include first-y;
        padding-top: 10px;
        @include clearfix();
    }

    .story__heading {
        .text {
            @include font(20, 26, 500);
        }
    }

    .story__thumb {
        width: 265px;
        height: 150px;
        @include runaround(left, 0, 16, 5, 0)
    }

    .story__summary {
        // @include font(16, 22,500);
        padding-top: 10px;
        margin-top: 10px;
        border-top: 1px solid #e7e7e7;
        overflow: hidden;
    }

    .story--focus {
        .story__thumb {
            width: 100%;
            height: 350px;
            margin: 0;
            float: none;
        }

        .story__heading {
            padding-top: 10px;

            .text {
                @include font(28, 34, 500);
            }
        }

