// >  version 4
.breadcrumbs {
    position: relative;
    border-left: 5px solid $third-color;
    @include clearfix();
    height: 22px;
    overflow: hidden;
    a{
        position: relative;
        color: $third-color;
        padding-left: 30px;
        &:before{
            content:'';
            width: 10px;
            height: 15px;
            position: absolute;
            left: 13px;
            top: 6px;
            background-image: url('../img/arrow-'+$arrow-version+'.png');
            background-size: ($arrow-w + px) ($arrow-h + px);
            background-repeat: no-repeat;
            background-position: -0px -56px;
        }
        &:first-child:before{
            display: none;
        }
    }
    .is-active{
        color: #000;
    }
    .cate {
        @include font(14, 22, $normal);
        display: inline-block;
        float: left;
        text-transform: uppercase;
        padding-top: 2px;
        color: $first-color;
        &:hover {
            color: $first-color;
        }
        &:first-child{
            padding-top: 0;
            @include font(18,22);
            padding-left: 10px;
        }
    }
}
